import React from 'react';
import './App.css';
import Nav from './components/Nav';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { PageTransition } from '@steveeeie/react-page-transition';
import Page from './components/Page';
import Home from './components/Home';


function App() {
  return (
    <BrowserRouter>
      <Nav />  
      <Route
        render={({ location }) => {
          return (
            <PageTransition
              preset="cubeToLeft"
              transitionKey={location.pathname}>
              <Switch location={location}>
                <Route exact path="/" component={() => <Home />} />
                <Route path="/page1" component={() => <Page value={1}/>} />
                <Route path="/page2" component={() => <Page value={2}/>} />
                <Route path="/page3" component={() => <Page value={3}/>} />
                <Route path="/page4" component={() => <Page value={4}/>} />
                <Route path="/page5" component={() => <Page value={5}/>} />
                <Route path="/page6" component={() => <Page value={6}/>} />
              </Switch>
            </PageTransition>
          );
        }}
      />
    </BrowserRouter>
  );
}

export default App;
